import React from "react";
import ReactDom from "react-dom";
import faker from "faker";
import CommentDetail from "./CommentDetail";
import Approvalcard from "./ApprovalCard"

const App = () => {
  return (
    <div className="ui container comments">
      <Approvalcard>
          <CommentDetail author="Ram" timeAgo="Today at 4:45 AM"  comment="it was helpful" avatar={faker.image.avatar()}/>
      </Approvalcard>
      <Approvalcard>
      <CommentDetail author="jane" timeAgo="Today at 6:05 PM" comment="this blog is cool" avatar={faker.image.avatar()}/>
      </Approvalcard>
      <Approvalcard>
      <CommentDetail author="Laxman" timeAgo="Today at 9:35 AM" comment="thanx for posting" avatar={faker.image.avatar()}/>
      </Approvalcard>
      <Approvalcard>
      <CommentDetail author="Alex" timeAgo="Today at 2:25 PM" comment="content is super" avatar={faker.image.avatar()}/>
      </Approvalcard>
    </div>
  );
};
ReactDom.render(<App />, document.querySelector("#root"));
